import React from 'react';

const Info = (props) => {
    const {weather} = props;

    let place;
    if(weather.country === 'United States of America') {
        place = <h3>{weather.city} - USA</h3>
    } else {
        place = <h3>{weather.city} - {weather.country}</h3>
    }

    if(weather.temp) {
        return (
            <div className="info">
                <div className="place-date">
                    <div className="place">
                        {place}
                    </div>
                    <div className="date">
                        <h3>{weather.currentDate}</h3>
                    </div>
                </div>
                <div className="icon-temp-condition">
                    <div className="icon-container">
                        <img src={weather.icon} alt="icon"/>
                    </div>
                    <div className="temp-condition">
                        <h2 id="temp">{weather.temp}</h2>
                        <h3>{weather.condition}</h3>
                    </div>
                </div>
                <div className="humidity-wind">
                    <h3>Humidity: {weather.humidity}</h3>
                    <h3>Wind: {weather.wind}</h3>
                </div>
                <div className="feels-like">
                    <h3>Feels like: {weather.feelsLike}</h3>
                </div>
            </div>
        )
    } else {
        return null;
    }
}

export default Info;